import Vue from 'vue'
import VueRouter from 'vue-router'
import NProgress from 'nprogress'
import 'nprogress/nprogress.css'


Vue.use(VueRouter)

const routes = [
  {
    path:'/login',
    component: () => import('@/views/Login')
  },
  {
    path: '/',
    component: () => import('@/Layout'),
    redirect:'/home',
    children:[
      { path:'/home', component: () => import('@/views/Home')},
      { path:'/overview', component: () => import('@/views/Overview')},
      { path:'/create-commodity', component: () => import('@/views/CreateCommodity')},
      { path:'/create-commodity/edit/:id', component: () => import('@/views/CreateCommodity'), props:true},
      { path:'/commodity-list', component: () => import('@/views/CommodityList')},
      { path:'/category', component: () => import('@/views/Category')},

      { path:'/module-editor', component: () => import('@/views/Module/editor.vue')},
      { path:'/module-expressInfo', component: () => import('@/views/Module/expressInfo.vue')},
      { path:'/module-count', component: () => import('@/views/Module/count.vue')},
      { path:'/module-cutting', component: () => import('@/views/Module/cutting.vue')},
      { path:'/module-excel', component: () => import('@/views/Module/excel.vue')},
    ]
  }
]

const router = new VueRouter({
  routes
})


//  判断是否登陆了  没登陆就只能访问 login
router.beforeEach((to, from, next) =>{
const user = JSON.parse(window.localStorage.getItem('user'))

  if(to.path !== '/login'){
    if(user){
      NProgress.start();
        next()
    }else{
      NProgress.start();
      next('/login')
    }
  }else{
    NProgress.start();
    next()
  }
})

router.beforeEach((to,from,next)  => {
  NProgress.done();
  next()
})

export default router
